* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  height: 100vh;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-item {
  display: flex;
  align-items: center;
  width: 50vw;
  height: 50px;
}
.country-name {
  margin-left: 10px;
}
.country-name-in-info {
  font-style: italic;
  font-weight: 900;
  font-size: 100;
  margin-left: 20px;
}
.main-info {
  display: flex;
  align-items: center;
  justify-content: center;
}
.other-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  font-size: 44px;
}
